<script>
    import WG_player_view from '../components/player_view.vue'
    import { toRef } from '@vue/reactivity';
    export default ({
        name : "WG_player_list_container",
        components :
        {
            WG_player_view,
        },
        props:
        {
            WG_player_list:
            {
                type: [Array, Object],
                required:true
            }
        },
        setup(props)
        {
            const playerList = toRef(props, "WG_player_list");
            const ringAudio = require("../assets/Water_Drop.mp3");
            const player = new Audio();
            player.src = ringAudio;
            

            const ring = ()=>
            {
                player.play()
            }

            return{
                player,
                playerList,
                ring
           }
        },
        watch:
        {
            playerList()
            {
               this.ring()
            }
        }        
    })
</script>

<template>
    <div id="player_list_container">
        <h1>Player List</h1>
        <div id ="container_player">
            <WG_player_view v-for="(player, index) in this.playerList" 
            v-bind:key="index" 
            v-bind:WG_pseudo="player.pseudo" 
            v-bind:WG_point="player.point"
            v-bind:WG_hasFinished="player.hasFinished"/>
        </div>
    </div>
</template>

<style scoped>
#player_list_container
{
    background-color: #11222A;  
    width : 20%;
    min-width: 200px;
    height : 100%;

}

h1
{
    color : #eaeaea;
    text-align: center;
}

#container_player
{
    height : 70%;
    box-shadow: rgba(1,1,1,0.5) -100px inset;
    overflow-y:scroll;
}

::-webkit-scrollbar {
  background: #11222A;
}

</style>